.promo {
  background-color: #161c36;
  background-repeat: no-repeat;
  background-image: url("../img/globe-back-mobile@1x.jpg");
  background-position: 50% -45%;
  background-size: 100%;
  position: relative;
  z-index: 10;
  color: $text-color-light;
  padding-top: 22px;
  padding-bottom: 40px;

  // Background "traveller"
  &::before {
    content: " ";
    position: absolute;
    width: 192px;
    height: 255px;
    background-image: url("../img/traveller-illustration-mobile@1x.png");
    background-size: 100%;
    background-repeat: no-repeat;
    top: 0;
    left: 50%;
    transform: translate(-42%, 59%);
    z-index: -1;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/traveller-illustration-mobile@2x.png");
    }
  }

  // Backgroung color for bottom of the section
  @media (max-width: $mobile-width-only) {
    &::after {
      content: " ";
      position: absolute;
      width: 100%;
      height: 28%;
      background-color: #182044;
      bottom: 0;
      left: 0;
      z-index: -2;
    }
  }

  @media (min-width: $tablet-width) {
    background-color: #182044;
    padding-top: 32px;
    padding-bottom: 90px;
    background-image: url("../img/globe-back-tablet@1x.jpg");
    background-position: 50% 50%;
    background-size: 200%;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/globe-back-tablet@2x.jpg");
    }

    // Background "traveller"
    &::before {
      width: 372px;
      height: 450px;
      background-image: url("../img/traveller-illustration-tablet@1x.png");
      transform: translate(-26.5%, 60.7%);

      @media (min-resolution: 2dppx) {
        background-image: url("../img/traveller-illustration-tablet@2x.png");
      }
    }
  }

  @media (min-width: $desktop-width) {
    padding-top: 84px;
    padding-bottom: 124px;
    background-color: #161c36;
    background-image: url("../img/globe-back-desktop@1x.jpg");
    background-position: 50% 100%;
    background-size: 125%;
    border-radius: 0 0 50px 50px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/globe-back-desktop@2x.jpg");
    }

    &::before {
      width: 430px;
      height: 640px;
      background-image: url("../img/traveller-illustration-desktop@1x.png");
      transform: translate(-27%, -2.5%);

      @media (min-resolution: 2dppx) {
        background-image: url("../img/traveller-illustration-desktop@2x.png");
      }
    }
  }
}

.promo__slogan-blockquote-wrapper {
  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    margin-bottom: 210px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 82px;
  }
}

.promo__slogan {
  @include typography(36px, 700, 1.12);
  margin: 0 auto;
  text-align: center;
  width: 90%;
  margin-bottom: 244px;

  @media (min-width: $tablet-width) {
    @include typography(60px, 700, 1.17);
    width: 60%;
    margin-bottom: 84px;
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    margin: 0;
    padding-left: 68px;
    padding-right: 68px;
    text-align: left;
  }
}

.promo__contacts {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}

.promo__user-list {
  margin: 0;
}

.promo__contacts-user-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto 26px auto;
  position: relative;
  justify-content: center;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.promo__blockquote {
  margin: 0;
  padding-right: 25px;
  padding-left: 25px;
  width: 80%;
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    width: 50%;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 25%;
    padding-top: 12px;
    padding-left: 44px;
    padding-right: 20px;
  }
}

.promo__blockquote-text {
  margin: 0;
  margin-bottom: 22px;

  @media (min-width: $tablet-width) {
    margin-bottom: 46px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 44px;
  }
}

.promo__blockquote-author {
  margin: 0;
  font-style: normal;
  font-weight: 700;
  display: inline-block;

  @media (min-width: $tablet-width) {
    font-size: 21px;
    line-height: 1.19;
  }
}

.promo__guide-list {
  margin: 0;
}
