.user-plan {
  padding-top: 28px;
  padding-bottom: 80px;
  background-color: #dfe3f0;
  border-radius: 30px 30px 0 0;

  @media (min-width: $tablet-width) {
    padding-bottom: 126px;
    padding-top: 56px;
  }

  @media (min-width: $desktop-width) {
    border-radius: 50px 50px 0 0;
    padding-bottom: 114px;
    padding-top: 54px;
  }
}

.user-plan__content-wrapper {
  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    padding-left: 34px;
    padding-right: 34px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 64px;
  }
}

.user-plan__heading {
  @include typography(24px, 700, 1);
  margin: 0;
  color: $button-text-color-dark;
  margin-bottom: 28px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    margin-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 6px;
  }
}

.user-plan__list {
  margin: 0;
  padding: 0;
  list-style-position: inside;
  color: $button-text-color-dark;
  font-weight: 700;
  counter-reset: numbers;
}

.user-plan__list-item {
  padding: 22px 25px;
  background-color: #ffffff;
  margin-bottom: 10px;
  border-radius: 10px;
  list-style: none;
  position: relative;

  &::before {
    counter-increment: numbers;
    content: counter(numbers) ".";
    color: inherit;
    font-size: 16px;
    font-weight: 500;

    @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
      font-size: 20px;
      position: absolute;
      right: 30px;
      top: 42px;
    }

    @media (min-width: $desktop-width) {
      font-size: 20px;
      margin-right: 38px;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 34px 30px 10px 36px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    padding: 36px 40px 36px 42px;
  }
}

.user-plan__list-item:last-child {
  margin-bottom: 0;
}

.user-plan__list-item-heading {
  margin: 0;

  @include typography(16px, 700, 1);
  margin-bottom: 20px;
  display: inline-block;

  @media (min-width: $tablet-width) {
    font-size: 24px;
    margin-bottom: 52px;
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    margin-bottom: 0;
  }
}

.user-plan__list-item-description {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    position: relative;
  }

  @media (max-width: $mobile-width-only) {
    &::before {
      content: " ";
      position: absolute;
      width: calc(100% + 66px);
      height: 1px;
      top: 0;
      left: 0;
      transform: translateY(-20px);
      margin-left: -36px;
      margin-right: -30px;
      background-color: #cbced9;
      opacity: 0.3;
    }
  }

  @media (min-width: $desktop-width) {
    width: 55%;
    grid-template-columns: 30% 1fr 1fr;
  }
}

.user-plan__companions,
.user-plan__dates {
  @include typography(14px, 500, 1);
  letter-spacing: 0.06em;
  display: block;
  text-transform: uppercase;
  margin-bottom: 6px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    letter-spacing: 0.1em;
  }
}

.user-plan__dates {
  @media (max-width: $mobile-width-only) {
    margin-bottom: 24px;
  }

  @media (min-width: $tablet-width) {
    letter-spacing: 0.06em;
  }
}

.user-plan__plan-link {
  @include typography(14px, 500, 1);
  letter-spacing: 0.03em;
  text-transform: uppercase;
  text-decoration: none;
  color: rgba(#000000, 0.3);
  display: block;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    letter-spacing: 0.1em;
    text-align: right;
  }

  &:hover,
  &:focus {
    color: rgba(#000000, 0.6);
  }

  &:active {
    color: rgba(#000000, 0.1);
  }
}
